const BOTTOM_NAV_HEIGHT = 56
const FAB_BUTTON_HEIGHT = 48
const SAVE_MARGIN = 16
const IOS_PB = "var(--sab)"

const MOBILE_FAB_B_PADDING = `calc(${FAB_BUTTON_HEIGHT}px + ${BOTTOM_NAV_HEIGHT}px + ${
  SAVE_MARGIN * 2
}px + ${IOS_PB})`

const MOBILE_PAGE_B_PADDING = `calc(${BOTTOM_NAV_HEIGHT}px + ${SAVE_MARGIN}px + ${IOS_PB})`

export { BOTTOM_NAV_HEIGHT, FAB_BUTTON_HEIGHT, IOS_PB, MOBILE_FAB_B_PADDING, MOBILE_PAGE_B_PADDING }
